/* eslint import/no-extraneous-dependencies: 0 */

import { definition } from '@eui/component';
import { LitComponent, html } from '@eui/lit-component';
import style from './logo.css';

@definition('eui-sysbar-logo', {
  style,
  props: {
    theme: { attribute: true, default: 'light' },
    dir: { attribute: true },
  },
})
export default class Logo extends LitComponent {
  /**
   * @private
   * @description Handle event
   * @param {Object} event - Click event
   */
  handleEvent() {
    window.EUI.Router.goto(window.EUI.defaultRoute);
  }

  render() {
    const { loc } = window.EUI.Localizer;
    const { icon, shortName, longName } = this.options.data;
    const dir = this.dir || '';
    return html`
    <div class="${dir}" @click=${this}>
      ${icon ? html`<eui-v0-icon class="logoIcon" name="${icon}" color="white" size="21px"></eui-v0-icon>` : html`<eui-v0-icon name="econ" color="white"></eui-v0-icon>`}
      <span class="name-long product">${loc[longName] || 'E-UI Software Development Kit'}</span>
      <span class="name-short accronym">${loc[shortName] || 'E-UI SDK'}</span>
    </div>`;
  }
}
